










import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import debounce from "lodash.debounce";
import TwoWay from "@/resources/mixins/two-way";

@Component({
    name: "MyInputDebounce"
})
export default class MyInputDebounce extends Mixins(TwoWay) {
    @Prop({ default: false })
    showWordLimit?: boolean;

    /** 内部绑定数据 */
    inputValue = "";
    emitValue = debounce(function(this: MyInputDebounce) {
        this.$nextTick(() => {
            this.currentValue = this.inputValue;
        });
    }, 750);

    @Watch("currentValue", {
        immediate: true
    })
    watchCurrentValue(): void {
        this.inputValue = this.currentValue;
    }

    @Watch("inputValue")
    watchInputValue() {
        this.inputValue = this.inputValue.trim();
        this.emitValue();
    }
}
